
import { defineComponent, PropType } from 'vue';
export default defineComponent({
	props: {
		icon: {
			type: String,
			required: true,
		},
		iconPos: {
			type: String as PropType<'left' | 'top'>,
			default: 'left',
		},
		disabled: {
			type: Boolean,
			default: false,
		},
	},
});
